import {
  getTypeObj, switchMethod
} from './options.js';
//
const panelStore = {
  state: {// 
    colNum: 24, // 
    rowHeight: 10, // px
    searchComps: { Select: true, Date: true }, // 
    compList: [], // 
    labelMap: {}, // 
    theme: 'default', // 
    //
    preView: false,
    activeComp: {}, // 
    datasetList: null, // 
  },
  mutations: {// state
    changeActiveComp(state, val) {
      if (state.preView) return;
      state.activeComp = val;
    },
    addComp(state, obj) { // 
      if (obj.type === 'Search' && obj.compType === 'Search') {
        obj.vm.$message({
          type: 'warning',
          message: `${obj.type} `
        });
        return;
      }
      if (state.searchComps[obj.type] && obj.compType !== 'Search') {
        obj.vm.$message({
          type: 'warning',
          message: `${obj.type} `
        });
        return;
      }
      if (!state.searchComps[obj.type] && obj.compType === 'Search') {
        obj.vm.$message({
          type: 'warning',
          message: `${obj.type} `
        });
        return;
      }
      const i = new Date().getTime();
      state.activeComp = getTypeObj(obj, i, state);
      // 
      if (obj.type === 'Search') { // 
        obj.list.push(state.activeComp);
      } else { // 
        obj.list.unshift(state.activeComp);
      }
    },
    switchComp(state, type) { // 
      const {
        x, y, w, h
      } = state.activeComp;
      const typeObj = getTypeObj({
        type, // 
        x,
        y,
      }, new Date().getTime(), state);
      Object.assign(typeObj, { w, h });
      // 
      switchMethod(state, typeObj);
      state.activeComp = typeObj;
    },
    setCompList(state, obj) { // 
      const { labelMap, compList, theme } = obj;
      state.compList = compList;
      state.labelMap = labelMap;
      state.theme = theme || 'default';
    },
    initState(state) {
      state.preView = false;// 
      state.compList = []; // 
      state.activeComp = {}; // 
      state.labelMap = {}; //
      state.theme = 'default';// 
      state.datasetList = null;// 
    }
  }
};
export default panelStore;
