<template>
  <div :class="['compInfo']">
    <el-form
      label-position="right"
      label-width="130px">
      <div class="title"></div>
      <el-form
        label-width="50px">
        <template>
          <el-form-item label="">
            <el-input
              v-model="styles.label.text"
              placeholder=""/>
          </el-form-item>
          <el-form-item label="">
            <el-input
              v-model="styles.label.subText"
              placeholder=""/>
          </el-form-item>
        </template>
      </el-form>
      <div class="title"></div>
      <el-form-item label="">
        <el-switch
          v-model="styles.legend.show"
          active-color="#409EFF"
          inactive-color="#eee"/>
        <template v-if="styles.legend.show">
          <label class="position"></label>
          <el-select
            v-model="styles.legend.top"
            :style="{width:'68px'}"
            :popper-append-to-body="false">
            <el-option
              label=""
              value="top"/>
            <el-option
              label=""
              value="middle"/>
            <el-option
              label=""
              value="bottom"/>
          </el-select>
        </template>
      </el-form-item>
      <el-form-item label="">
        <el-switch
          v-model="styles.labelNormal.show"
          active-color="#409EFF"
          inactive-color="#eee"/>
        <template v-if="styles.labelNormal.show">
          <label class="position"></label>
          <el-select
            v-model="styles.labelNormal.position"
            :style="{width:'68px'}"
            :popper-append-to-body="false">
            <el-option
              label=""
              value="top"/>
            <el-option
              label=""
              value="bottom"/>
            <el-option
              label=""
              value="left"/>
            <el-option
              label=""
              value="right"/>
            <el-option
              label=""
              value="inside"/>
          </el-select>
        </template>
      </el-form-item>
      <template v-if="data.type==='Line'">
        <el-form-item label="">
          <el-radio
            v-model="styles.lineParams.smooth"
            :label="false"></el-radio>
          <el-radio
            v-model="styles.lineParams.smooth"
            :label="true"></el-radio>
        </el-form-item>
        <el-form-item label="">
          <el-radio
            v-model="styles.lineParams.areaStyle"
            :label="null"></el-radio>
          <el-radio
            v-model="styles.lineParams.areaStyle"
            :label="true"></el-radio>
        </el-form-item>
        <el-form-item label="">
          <el-radio
            v-model="styles.lineParams.stack"
            :label="null"></el-radio>
          <el-radio
            v-model="styles.lineParams.stack"
            label=""></el-radio>
        </el-form-item>
      </template>
      <template v-if="data.type==='Bar'">
        <el-form-item label="">
          <el-radio
            v-model="styles.barParams.vertical"
            :label="false"></el-radio>
          <el-radio
            v-model="styles.barParams.vertical"
            :label="true"></el-radio>
        </el-form-item>
        <el-form-item label="">
          <el-radio
            v-model="styles.barParams.stack"
            :label="null"></el-radio>
          <el-radio
            v-model="styles.barParams.stack"
            label=""></el-radio>
        </el-form-item>
      </template>
      <template v-if="data.type==='Line'||data.type==='Bar'||data.type==='Comb'">
        <div class="title"></div>
        <ul class="axis">
          <li
            :class="{'active':axisType==='x'}"
            @click="axisType='x'">{{ axisName.x }}</li>
          <li
            :class="{'active':axisType==='y'}"
            @click="axisType='y'">{{ axisName.y }}</li>
          <li
            v-if="styles.xAxis[0].select.length<2"
            :class="{'active':axisType==='yy'}"
            @click="axisType='yy'">{{ axisName.yy }}</li>
        </ul>
        <el-form
          label-width="105px">
          <br>
          <el-form-item label="">
            <el-switch
              v-model="styles[axis.type][axis.index].show"
              active-color="#409EFF"
              inactive-color="#eee"/>
          </el-form-item>
          <template v-if="data.type==='Comb'">
            <template v-if="axisType==='y'">
              <el-form-item label="">
                <el-radio
                  v-model="styles.barParams.stack"
                  :label="null"></el-radio>
                <el-radio
                  v-model="styles.barParams.stack"
                  label=""></el-radio>
              </el-form-item>
            </template>
            <template v-if="axisType==='yy'">
              <el-form-item label="">
                <el-radio
                  v-model="styles.lineParams.smooth"
                  :label="false"></el-radio>
                <el-radio
                  v-model="styles.lineParams.smooth"
                  :label="true"></el-radio>
              </el-form-item>
              <!-- <el-form-item label="">
                <el-radio
                  v-model="styles.lineParams.areaStyle"
                  :label="null"></el-radio>
                <el-radio
                  v-model="styles.lineParams.areaStyle"
                  :label="true"></el-radio>
              </el-form-item>
              <el-form-item label="">
                <el-radio
                  v-model="styles.lineParams.stack"
                  :label="null"></el-radio>
                <el-radio
                  v-model="styles.lineParams.stack"
                  label=""></el-radio>
              </el-form-item> -->
            </template>
          </template>
          <template>
            <el-form-item label="">
              <el-input
                v-model="styles[axis.type][axis.index].name"
                placeholder=""/>
            </el-form-item>
            <el-form-item :label="axis.name+''">
              <el-select
                multiple
                :placeholder="''+axis.name"
                :multiple-limit="axis.limit"
                :collapse-tags="true"
                :popper-append-to-body="false"
                value-key="label"
                v-model="styles[axis.type][axis.index].select">
                <el-option
                  :label="obj.label"
                  :key="idx"
                  v-for="(obj,idx) in metricsFilter"
                  :value="obj"/>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-switch
                v-model="styles[axis.type][axis.index].splitLine.show"
                active-color="#409EFF"
                inactive-color="#eee"/>
            </el-form-item>
            <el-form-item label="">
              <el-switch
                v-model="styles[axis.type][axis.index].axisLabel.show"
                active-color="#409EFF"
                inactive-color="#eee"/>
            </el-form-item>
          </template>
        </el-form>
      </template>
    </el-form>
  </div>
</template>
<script>

export default {
  props: {
    data: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      axisType: 'x',
      styles: {},
    };
  },
  computed: {
    axisName() {
      const val = {
        x: 'X',
        y: 'Y',
        yy: 'Y'
      };
      if (this.data.type === 'Bar' && !this.styles.barParams.vertical) {
        val.x = 'Y';
        val.y = 'X';
        val.yy = 'X';
      }
      if (this.data.type === 'Comb') {
        val.y = 'Y()';
        val.yy = 'Y()';
      }
      return val;
    },
    axis() {
      let limit = 0;
      if (this.axisType === 'x') {
        const h = this.styles.yAxis[0].select.length + this.styles.yAxis[1].select.length;
        limit = h > 1 ? 1 : 2;
        return {
          name: '',
          type: 'xAxis',
          limit,
          index: 0,
        };
      }
      //
      if (this.styles.xAxis[0].select.length > 1) {
        limit = 1;
      }
      if (this.axisType === 'y') {
        return {
          name: '',
          type: 'yAxis',
          limit,
          index: 0,
        };
      }
      return {// yy
        name: '',
        type: 'yAxis',
        limit,
        index: 1,
      };
    },
    metricsFilter() {
      if (!this.data.params.dimensions) {
        return [];
      }
      let array = [];
      if (this.axisType === 'x') {
        array = this.data.params.dimensions;
      } else {
        const { select } = this.styles.yAxis[this.axisType === 'y' ? 1 : 0];
        array = this.data.params.metrics.filter((item) => {
          let result = true;
          for (let i = 0, j = select.length; i < j; i += 1) {
            if (select[i].label === item.label) {
              result = false;
              break;
            }
          }
          return result;
        });
      }
      return array;
    }
  },
  created() {
    this.styles = this.data.styles;
  }
};
</script>
