import XLSX from 'xlsx';

// ArrayBuffer
function s2ab(s) {
  const buf = new ArrayBuffer(s.length);
  const view = new Uint8Array(buf);
  // eslint-disable-next-line no-bitwise
  for (let i = 0; i < s.length; i += 1) view[i] = s.charCodeAt(i) & 0xFF;
  return buf;
}
// sheetexcelblobURL.createObjectURL
function sheet2blob(sheet, sheetNames) {
  const sheetName = sheetNames || 'sheet1';
  const workbook = {
    SheetNames: [sheetName],
    Sheets: {}
  };
  workbook.Sheets[sheetName] = sheet;
  // excel
  const wopts = {
    bookType: 'xlsx', // 
    bookSST: false, // Shared String TableIOS
    type: 'binary'
  };
  const wbout = XLSX.write(workbook, wopts);
  const blob = new Blob([s2ab(wbout)], { type: 'application/octet-stream' });
  return blob;
}
/**
 * 
 * @param url blob
 * @param saveName 
 */
function openDownloadDialog(url, saveName) {
  let urlVal = url;
  if (typeof urlVal === 'object' && urlVal instanceof Blob) {
    urlVal = URL.createObjectURL(urlVal); // blob
  }
  const aLink = document.createElement('a');
  aLink.href = urlVal;
  aLink.download = saveName || ''; // HTML5file:///
  let event;
  if (window.MouseEvent) event = new MouseEvent('click');
  else {
    event = document.createEvent('MouseEvents');
    event.initMouseEvent('click', true, false, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);
  }
  aLink.dispatchEvent(event);
}
export default (data, sheetName, title) => {
  // var aoa = [
  //   ['', null, null, ''], // 2null
  //   ['', '', '', ''],
  //   ['', '', 18, new Date()],
  //   ['', '', 22, new Date()]
  // ];
  // var sheet = XLSX.utils.aoa_to_sheet(aoa);
  // sheet['!merges'] = [
  //   // A1-C1
  //     {s: {r: 0, c: 0}, e: {r: 0, c: 2}}
  // ];
  // const data = [
  //   ['', '', '', ''],
  //   ['', '', 18, new Date()],
  //   ['', '', 22, new Date()]
  // ];
  if (!(data instanceof Array)) {
    return;
  }
  const sheet = XLSX.utils.aoa_to_sheet(data);
  const url = sheet2blob(sheet, sheetName);
  openDownloadDialog(url, title || '.xlsx');
};
